
.header-image {
  display: block;
  width: 100%;
  text-align: center;
  background: url("../assets/header.png") no-repeat top center scroll;
  background-size: cover;
  -o-background-size: cover;
  height: 500px;
}
@media only screen and (min-width: 1900px) {
.header-image {
      height: 600px;
}
}
.headline {
  padding: 0 0;
  color: #231f20;
}
.headline h1 {
    padding: 10px;
    margin-top: 200px;
    font-size: 70px;
    background: rgba(248, 222, 26, 0.3);
}
.headline h2 {
    padding: 10px;
    font-size: 35px;
    background: rgba(248, 222, 26, 0.3);
    margin-bottom: 100px;
}



