
nav {
  background-color: #f8de1a;
}
nav ul.nav .list-enter-active {
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
}
nav ul.nav .list-enter,
  nav ul.nav .list-leave-to {
    opacity: 0;
    -webkit-transform: translateX(-30px);
            transform: translateX(-30px);
}
nav ul.nav a,
  nav ul.nav a:hover,
  nav ul.nav a:focus {
    color: #231f20;
    font-weight: bolder;
}
nav ul.nav a:hover,
  nav ul.nav a:active,
  nav ul.nav a:focus {
    background-color: #f4ad1c;
}
nav .icon-bar {
    background-color: black;
}
.navbar-brand {
  padding: 0 !important;
}
.navbar-brand img {
    max-width: 250px;
    transition: all 0.4s;
}
@media (max-width: 991px) {
.navbar-brand img {
        max-width: 100px;
}
}
.navbar-brand-small img {
  transition: all 0.4s;
  max-width: 100px;
}



